<template>
  <div class="bar-update">
    <img
      v-if="displayType === 0"
      class="icon-pre"
      @click="goPreApply"
      :src="data.images.length ? data.images[0].imgUrl : 'http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_three_bird_pre.png'">
    <template v-else>
      <img
        class="icon-experience"
        @click="goSybird(1)"
        src="http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_three_bird_ experience.png"/>
      <div class="flex-column">
        <img
          @click="goSybird(2)"
          src="http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_three_bird_guanjia.png"/>
        <img
          @click="goSybird(3)"
          src="http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_three_bird_vip.png"/>
      </div>
    </template>
  </div>
</template>
<script>
/** 【精选】三翼鸟模块 */
export default {
  props: {
    displayType: { // 0--预约报名 1--三翼鸟介绍
      type: Number,
      default: 0,
    },
    data: {
      type: Object,
      default: () => {},
    },
    storeCode: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
    };
  },
  methods: {
    goSybird(type) {
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '三翼鸟介绍',
        modelCode: 'sa_intr',
        site_number: type + '',
        itemCode: 'null',
      })
      this.$router.push(`/shopSybird?showType=${type}`);
    },
    goPreApply() { // 三翼鸟报名页面
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '三翼鸟模块 订单提报',
        modelCode: 'sa_order',
        site_number: '1',
        itemCode: 'null',
      })
      if (this.$util.isUplus()) {
        this.$util.openNewWindow(`mpaas://homedesign?needAuthLogin=1&originShopCode=${this.storeCode}`, true, true);
      } else {
        this.$dialog.alert({
          message: '请下载海尔智家APP进行操作'
        });
      }
    }
  },
}
</script>
